import React from 'react';
import Text from './Text';
import UserImage from './UserImage';

class Header extends React.Component {
  state = {
    components: [
        {"Component":"Text",
            "InputType":"text",
        "ClassName":"HeaderSearch",
        "PlaceHolder":"Search"},
        {"Component":"Image",
        "alt":"UserLogin",
        "ClassName":"userLogo",
        "src":"data:image/png;base64,"
        },
        {"Component":"Image",
            "alt":"UserLogin",
            "ClassName":"userLogo2",
            "src":"data:image/png;base64,"
            },
            {"Component":"Image",
                "alt":"UserLogin",
                "ClassName":"userLogo3",
                "src":"data:image/png;base64,"
                }
    ]
  };


  renderComponent = (component, index) => {
    const { Component, ClassName, InputType,PlaceHolder } = component;
    if (Component === 'Image') {
      return <UserImage key={index} className={ClassName} {...component}/>;
    } else if (Component === 'Text') {
      return <Text key={index} className={ClassName} inputType={InputType} placeholder={PlaceHolder}/>;
    }
    return null;
  };

  render() {
    return (
      <div className='header'>
        {this.state.components.map((component, index) => (
          this.renderComponent(component, index)
        ))}
      </div>
    );
  }
}

export default Header;
